.peopleCards {
  display: grid;
  grid-template-columns: repeat(auto-fill, 299px);
  grid-gap: 50px 70px;
  justify-content: center;
}
.peopleCard {
  height: 400px;
  border-radius: 20px;
  background-color: white;
  &-img {
    border-radius: 20px 20px 0 0;
  }
  &-info {
    padding: 15px;
    text-align: center;
  }
  &-name {
    font-size: 26px;
    font-family: 'Aeroport-Bold';
    margin-bottom: 12px;
  }
}
