.сomparison{color:#fff;font-family:"Aeroport"}.сomparison-prod{display:flex;justify-content:space-between;padding-bottom:30px;border-image:linear-gradient(to right, #5584ff, #9d60ff) 47% 0%;border-bottom:.5px solid}.сomparison-prodLeft{font-size:16px}.сomparison-prodLeftCount{color:#d9d9d9}.сomparison-dropdownControl{font-size:18px;background:#fff;width:285px;color:#000;cursor:pointer;line-height:50px;border-radius:5px;padding:0 20px;display:flex;justify-content:space-between;align-items:center;margin-top:30px}.сomparison-dropdownMenu{font-size:18px;background:#fff;width:285px;color:#000;cursor:pointer;line-height:50px;border-radius:5px;padding:0 20px;position:absolute;z-index:2}.сomparison-dropdownClosed,.сomparison-dropdownOpen{border:solid #000;border-width:0 1px 1px 0;display:inline-block;padding:3px}.сomparison-dropdownClosed{top:8px;transform:rotate(45deg);-webkit-transform:rotate(45deg)}.сomparison-dropdownOpen{top:12px;transform:rotate(-455deg);-webkit-transform:rotate(-135deg)}.сomparison-prodLeftCheckbox{margin-top:20px}.сomparison-prodLeftCircles{display:flex;margin:160px 0 0 30px}.сomparison-prodLeftCircle{width:50px;height:50px;background-color:#ff0100;border-radius:50%}.сomparison-prodLeftCircle:hover{background-color:#ff8585}.сomparison-prodLeftCircle:last-child{margin-left:20px}.сomparison-prodRight{display:flex;flex-direction:column;align-items:flex-end;cursor:pointer}.сomparison-prodRightTitle{margin-bottom:30px}.сomparison-prodRightDelete{margin-left:20px;font-family:"Aeroport-Light";font-size:14px}.сomparison-prodRightCarousel{display:flex}.сomparison-characteristic{background:#fff;border-radius:20px;margin-top:42px;padding:30px 20px 20px;color:#000}.сomparison-characteristicTitle{margin:0 0 40px 30px;font-family:"Aeroport-Medium";font-size:28px}.сomparison-characteristicSub{width:300px;padding:12px 30px}.сomparison-characteristicSub:not(:last-child){margin-bottom:28px;padding:12px 30px}.сomparison-characteristicSub:hover{background:#1f1f1f;color:#fff;transition:.4s;width:100%;position:relative}.сomparison-characteristicBlock{display:flex}