body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 18px;
  background: #1f1f1f;
  font-family: 'Aeroport';
}

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

ul[class],
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  font-weight: normal;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

img,
input,
textarea,
button,
select,
div,
a {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

input,
textarea {
  text-indent: 10px;
  outline: none;
}
textarea {
  padding-top: 14px;
  resize: none;
}
input[type='checkbox'] + label {
  cursor: pointer;
}
input[type='checkbox'] {
  display: none;
}
input[type='checkbox'] + label:before {
  content: '\2714';
  margin-right: 20px;
  width: 20px;
  height: 20px;
  background-color: #f6f6f6;
  border-radius: 50%;
  display: inline-block;
  vertical-align: bottom;
  color: transparent;
  transition: 0.2s;
}
input[type='checkbox']:checked + label:before {
  background-color: red;
  border-color: red;
  color: #fff;
  font-size: 11px;
  padding: 2.4px 5.5px;
}
a[href^='tel:'] {
  text-decoration: none;
}
::-webkit-scrollbar {
  width: 7px;
  background-color: #f9f9fd;
}
::-webkit-scrollbar-thumb {
  border-radius: 2px;
  background-color: black;
}
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.2);
  border-radius: 7px;
  background-color: #f9f9fd;
}

.PageBottom {
  padding-bottom: 145px;
}
.NavLink {
  color: white;
  text-decoration: none;
}
.btnGeneral {
  font-size: 14px;
  width: 200px;
  height: 50px;
  border-radius: 30px;
  border: 0;
  font-family: 'Aeroport-Medium';
  cursor: pointer;
}
@media (max-width: 1200px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 1000px) {
  .container {
    padding: 0 25px;
  }
}
@media (max-width: 480px) {
  .container {
    padding: 0 16px;
  }
  .PageBottom {
    padding-bottom: 145px;
  }
}

